import React from "react";

const Card = ({ name, email, id }) => {
  //   const { name, email, id } = item;
  return (
    <div>
      {/* <pre>{JSON.stringify(item, null, 4)}</pre> */}
      <div className="bg-warning text-white text-center ">
        {/* img */}
        <img
          src={`https://robohash.org/kz${id} `}
          alt={name}
          height={"200px"}
        />
        <h1>{name}</h1>
        <p>{email}</p>
      </div>
    </div>
  );
};

export default Card;
