import React from "react";

const AddForm = ({ name, email, setName, setEmail }) => {
  return (
    <div>
      {/* <pre>{JSON.stringify({ name, email }, null, 4)}</pre> */}
      <div className="container pt-2">
        {/* <div className="col-md-4 offset-md-4 bg-warning p-1"> */}
        <div className=" bg-warning p-1">
          <form className="form-group">
            <div className="mb-3">
              <label className="mb-1 ms-1" htmlFor="name">
                Name
              </label>
              <input
                type="text"
                id="name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="e.g John Huwai"
                className="form-control form-control-lg "
              />{" "}
            </div>

            <div className="mb-3">
              <label className="mb-1 ms-1" htmlFor="email">
                Email
              </label>
              <input
                type="email"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
                id="email"
                placeholder="e.g JohnHuwai@gmail.com"
                className="form-control form-control-lg "
              />{" "}
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default AddForm;
