import React, { useState, useEffect } from "react";
// import "./App.css";
import details from "./utils/details.json";
import "bootstrap/dist/css/bootstrap.min.css";
import Card from "./component/Card";
import AddModal from "./component/AddModal";

function App() {
  const [user, setUser] = useState(details);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [mySearch, setMySearch] = useState("");

  const AddtoUser = (e) => {
    // let id = Date.now();
    setUser((e) => {
      return [{ id: Date.now(), name, email }, ...e];
    });
  };
  const searchUser = (e) => {
    let filters = !mySearch
      ? details
      : details.filter((e) =>
          e.name.toLocaleLowerCase().includes(mySearch.toLocaleLowerCase())
        );
    setUser(filters);
  };
  useEffect(
    (e) => {
      searchUser();
    },
    [mySearch]
  );
  return (
    <>
      {true ? (
        <div className="container-fluid bg-secondary">
          <div className="d-flex justify-content-center py-4">
            <input
              placeholder="Search"
              type="text"
              className="py-2 col-md-4 "
              onChange={(e) => setMySearch(e.target.value)}
              value={mySearch}
            />
          </div>{" "}
          <div className="row">
            <div className="col-md-1 ">
              {/* <h4>+ Add</h4> */}
              <AddModal
                AddtoUser={AddtoUser}
                setName={setName}
                setEmail={setEmail}
                name={name}
                email={email}
              />
            </div>
            <div className="col-md-11">
              <div className="row">
                {user.map((item) => (
                  <div key={item.id} className="col-md-4 col-sm-6">
                    <Card name={item.name} email={item.email} id={item.id} />
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      ) : (
        <>{/* <AddForm /> */}</>
      )}
      {/* // <AddModal /> */}
    </>
  );
}

export default App;
